@import url(https://fonts.googleapis.com/css?family=Ubuntu);
.pj-container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;              /* âœ… ÎµÏ€Î¹Ï„ÏÎ­Ï€ÎµÎ¹ Î½Î± â€œÏƒÏ€Î¬Î½Îµâ€ Î¿Î¹ ÎºÎ¬ÏÏ„ÎµÏ‚ ÏƒÎµ Î½Î­ÎµÏ‚ ÏƒÎµÎ¹ÏÎ­Ï‚ */
    gap: 24px;                    /* Î±Ï€ÏŒÏƒÏ„Î±ÏƒÎ· Î¼ÎµÏ„Î±Î¾Ï project boxes */
    text-align: center;
    color: white;
    font-family: 'Ubuntu', sans-serif;
}
.pj-card {
  flex: 1 1 300px;              /* responsive Ï€Î»Î¬Ï„Î¿Ï‚ â€“ Ï„Î¿Ï…Î»Î¬Ï‡Î¹ÏƒÏ„Î¿Î½ 300px */
  max-width: 350px;
  background: #2a2a2a;
  border-radius: 10px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s ease;
}

.pj-card:hover {
  transform: translateY(-5px);
}

.pj-thumb {
  width: 100%;
  height: auto;
  border-radius: 8px;
  display: block;
}

.pj-card h3 {
  margin: 12px 0 6px;
  font-size: 20px;
}

.pj-card p {
  font-size: 14px;
  opacity: 0.85;
}

/* ğŸ“± Responsive Breakpoints */

/* Tablet */
@media (max-width: 992px) {
  .pj-container {
    justify-content: center;
    gap: 20px;
  }

  .pj-card {
    flex: 1 1 45%;              /* Î´ÏÎ¿ projects Î±Î½Î¬ ÏƒÎµÎ¹ÏÎ¬ */
  }
}

/* ÎšÎ¹Î½Î·Ï„ÏŒ */
@media (max-width: 600px) {
  .pj-container {
    flex-direction: column;
    align-items: center;
    padding: 16px;
  }

  .pj-card {
    flex: 1 1 100%;
    max-width: 90%;
  }

  .pj-card h3 {
    font-size: 18px;
  }
}
